import {mapGetters} from 'vuex';

export default {
    name: "app-address-bar",
    props: {
        address: {
            default: null,
        },
        allZiti: {
            default: false,
        },
        hasZiti: {
            default: false,
        },
        hasCity: {
            default: false,
        },
        city: {
            default: null
        }
    },
    computed: {
        ...mapGetters('mallConfig', {
            theme: 'getTheme'
        }),
    },
    methods: {
        navigateAddress() {
            if (this.city && this.city.errorCode === 1) {
                uni.showModal({
                    content: this.city.error,
                    showCancel: false
                });
                return ;
            }
            uni.navigateTo({
                url: '/pages/order-submit/address-pick?hasCity=' + this.hasCity,
            });
        },
        handleAddressInput() {
            this.$emit('addressInput', this.address);
        },
    },
}
